const CharacterContent: React.FC = () => {
  useEffect(() => {
    const navBar = document.querySelectorAll<HTMLElement>('.character-card')!;
    
    const animHead = (heading: HTMLElement, delay: number) => {
      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }

    function activeScrollTrigger(obj:HTMLElement, delay:number){
   
      obj.style.opacity = '0'
       
          ScrollTrigger.create({
              trigger: obj,
              start: `100 80%`,
              end: `200 5%`,
             /*  markers: true, */
              onEnter: ()=>{
                  animHead(obj, delay)
              },
               
              
          });
    }
    navBar.forEach((el,num)=> {
      if(num === 0) {
        activeScrollTrigger(el, 1)
      }
      else  activeScrollTrigger(el, (num + 1.2)/2)
      
    })
    
    
    
  }, []);
  return (
    <section className="characters-container">
      {characters.map((character, index) => (
        <CharacterCard key={index} character={character} />
      ))}
    </section>
  );
};

export default CharacterContent;
