import React, { useEffect, useState } from "react";
import "../scss/Tabs.scss";
import CharacterContent from "./CharacterContent";
import ItemsContent from "./ItemsContent";
import NFTContent from "./NFTContent";
import AccountSettingsContent from "./AccountSettingsContent";



const NavBar: React.FC = () => {
  const [activeTab, setActiveTab] = useState<string>("Character");

  const handleTabClick = (tabName: string) => {
    setActiveTab(tabName);
  };

  const renderContent = () => {
    switch (activeTab) {
      case "Character":
        return <CharacterContent />;
      case "Items":
        return <ItemsContent />;
      case "NFT":
        return <NFTContent />;
      case "Account Settings":
        return <AccountSettingsContent />;
      default:
        return null;
    }
  };
  useEffect(() => {
    const navBar = document.querySelector('.navbar')! as HTMLElement;
    
    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }
  function activeScrollTrigger(obj:HTMLElement, delay:number){
   
    obj.style.opacity = '0'
     
        ScrollTrigger.create({
            trigger: obj,
            start: `100 80%`,
            end: `200 5%`,
           /*  markers: true, */
            onEnter: ()=>{
                animHead(obj, delay)
            },
             
            
        });
  }
  activeScrollTrigger(navBar, 1)


  }, []);
  
  return (
    <>
      <nav className="navbar">
        <ul className="navbar-list">
          <li
            className={`navbar-item ${
              activeTab === "Character" ? "active" : ""
            }`}
          >
            <a href="#" onClick={() => handleTabClick("Character")}>
              Character <sup>(2)</sup>
            </a>
          </li>
          <li
            className={`navbar-item ${activeTab === "Items" ? "active" : ""}`}
          >
            <a className='navbar-item-notLink' href="#"/*  onClick={() => handleTabClick("Items")} */>
              Items <sup>(Soon)</sup>
            </a>
          </li>
          <li className={`navbar-item ${activeTab === "NFT" ? "active" : ""}`}>
            <a className='navbar-item-notLink' href="#" /* onClick={() => handleTabClick("NFT")} */>
              NFT <sup>(Soon)</sup>
            </a>
          </li>
          <li
            className={`navbar-item ${activeTab === "Account Settings" ? "active" : ""}`}>
            <a className='navbar-item-notLink' href="#" /* onClick={() => handleTabClick("Account Settings")} */>
              Account settings <sup>(Soon)</sup>
            </a>
          </li>
        </ul>
      </nav>
      <div className="tab-content">{renderContent()}</div>
    </>
  );
};

export default NavBar;
