 
import "../scss/ProfileInfo.scss";
import PenSvg from "@/uiKit/personPage/ui-kit/PenSvg";
import WalletSvg from "@/uiKit/personPage/ui-kit/WalletSvg";
import CopySvg from "@/uiKit/personPage/ui-kit/CopySvg";
import { useEffect } from "react";
const ProfileInfo = () => {
  useEffect(() => {
    const profileImage = document.querySelector('.profile-image-container')! as HTMLElement;
    const loginContainer = document.querySelector('.login-container')! as HTMLElement;  
    const addressContainer = document.querySelector('.address-container')! as HTMLElement;  
    const emailContainer = document.querySelector('.email-container')! as HTMLElement;  
   
    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }
  function activeScrollTrigger(obj:HTMLElement, delay:number){
   
    obj.style.opacity = '0'
     
        ScrollTrigger.create({
            trigger: obj,
            start: `100 80%`,
            end: `200 5%`,
           /*  markers: true, */
            onEnter: ()=>{
                animHead(obj, delay)
            },
             
            
        });
  }
  const objEl = {
    img: [profileImage, 0.5],
    login:[loginContainer, 0.6],
    adress:[addressContainer, 0.7],
    email:[emailContainer, 0.8],
    
   
    
}
// eslint-disable-next-line @typescript-eslint/no-unused-vars
Object.entries(objEl).forEach(([_, value]) => {
   
    const obj = value[0] as HTMLElement;     
    const num = value[1] as number;
    activeScrollTrigger(obj, num);
});


  }, []);
  return (
    <section className="profile-info-container">
      <div className="profile-image-container">
        <img
          src="assets/personPage/ProfileInfo/avatar.png"
          className="profile-avatar"
        />
      </div>
      <div className="profile-container">
        <div className="login-container">
          <span className="login ">Harry Brasko</span>
          <PenSvg />
        </div>
        <div className="address-container">
          <WalletSvg />
          <span className="address">0xe32b....542d</span>
          <CopySvg />
        </div>
        <div className="email-container">
          <span className="header-email">E-mail</span>
          <span className="email">HarryBrasko@gmail.com</span>
          <PenSvg />
        </div>
      </div>
    </section>
  );
};

export default ProfileInfo;
